<template>
	<div class="quiz-item">
		<div class="quiz-item__body">
			<div class="quiz-item__header">
				<slot name="header"/>
			</div>
			<div class="quiz-item__quest">
				{{ question }}
			</div>
			<div
				v-if="answers.length > 0"
				class="quiz-item__answers"
			>
				<Btn
					v-for="(answer, i) in answers"
					:key="i"
					:class="{
						incorrectly: i === incorrectly,
						choosen: i === choosen,
						correctly: i === correctly
					}"
					class="quiz-item__answer"
					@click="choose(i)"
				>
					{{ answer.text }}
				</Btn>
			</div>
		</div>
	</div>
</template>

<script>
import Btn from '@components/btn'

export default {
	name: 'QuizItem',
	components: {
		Btn
	},
	props: {
		item: {
			type: Object,
			default: () => ({})
		},
		mode: {
			type: String,
			default: 'quiz'
		}
	},
	data() {
		return {
			correctly: false,
			incorrectly: false,
			choosen: false,
			delay: 1000,
			timeoutResult: null,
			timeoutNext: null
		}
	},
	computed: {
		question() {
			return this.item.question || ''
		},
		answers() {
			return this.item.answers || []
		}
	},
	unmounted() {
		clearTimeout(this.timeoutResult)
		clearTimeout(this.timeoutNext)
	},
	methods: {
		choose(index) {
			if (this.choosen === false) {
				const value = this.answers[index].value
				this.choosen = index

				this.timeoutResult = setTimeout(() => {
					if (this.mode === 'quiz') {
						if (value) {
							this.correctly = index
						} else {
							const correctIndex = this.answers.reduce((result, item, i) => item.value ? i : result, 0)
							this.incorrectly = index
							this.correctly = correctIndex
						}
					}
				}, this.delay)

				const waitTime = this.mode === 'test' ? .5 : 4

				this.timeoutNext = setTimeout(() => {
					this.$emit('next', value)
					this.clear()
				}, this.delay * waitTime)
			}
		},
		clear() {
			this.incorrectly = false
			this.correctly = false
			this.choosen = false
		}
	}
}
</script>

<style lang="stylus" src="@styl/quiz-item/index.styl"></style>
