<template>
	<div class="finish">
		<div class="finish__body">
			<div class="finish__title">Congratulations!</div>
			<div class="finish__text">
				<template
					v-if="mode === 'quiz'"
				>
					You answered <span class="finish__counter">{{ value }}/{{ max }}</span> questions
					<br>
					You earned <img src="@assets/star.png" alt="" class="finish__icon"> <span class="finish__score">{{ score }}</span> points
				</template>
				<template
					v-if="mode === 'test'"
				>
					{{ result }}
				</template>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Finish',
	props: {
		value: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 0
		},
		score: {
			type: Number,
			default: 0
		},
		results: {
			type: Array,
			default: () => ([])
		},
		mode: {
			type: String,
			default: 'quiz'
		}
	},
	computed: {
		result() {
			const results = [...this.results].sort((a, b) => b.max - a.max)
			let result = ''

			results.forEach(({ max, text }) => {
				if (this.value <= max) {
					result = text
				}
			})

			return result
		}
	}
}
</script>

<style lang="stylus" src="@styl/finish/index.styl"></style>
