<template>
	<div class="quiz">
		<transition name="slide">
			<Screen v-if="step === 'start'">
				<Start @play="play"/>
			</Screen>
			<Screen v-else>
				<Header v-if="step !== 'start'"/>

				<transition name="opacity">
					<QuizItem
						v-if="step === 'quiz'"
						:item="item"
						:mode="mode"
						@next="next"
					>
						<template #header>
							<Counter
								:current="activeId"
								:max="items.length"
							/>
							<Score
								v-if="mode === 'quiz'"
								:value="score"
							/>
						</template>
					</QuizItem>
				</transition>

				<transition name="opacity">
					<Finish
						v-if="step === 'finish'"
						:value="result"
						:score="score"
						:max="items.length"
						:mode="mode"
						:results="results"
					/>
				</transition>

				<Restart @restart="restart"/>
			</Screen>
		</transition>
	</div>
</template>

<script>
const gameMode = process.env.VUE_APP_GAME_MODE

import Screen from '@components/screen'
import Header from '@components/header'
import QuizItem from '@components/quiz-item'
import Start from '@components/start'
import Finish from '@components/finish'
import Score from '@components/score'
import Counter from '@components/counter'
import Restart from '@components/restart'

export default {
	name: 'App',
	components: {
		Screen,
		Header,
		QuizItem,
		Start,
		Finish,
		Score,
		Counter,
		Restart,
	},
	data() {
		return {
			items: [],
			results: [],
			values: [],
			activeId: 0,
			step: 'start',
			cost: 200,

			mode: gameMode
		}
	},
	computed: {
		item() {
			return this.items.length > 0 ? this.items[this.activeId] : false
		},
		result() {
			return this.values.reduce((total, value) => total + value, 0)
		},
		score() {
			return this.values.reduce((total, value) => total + value, 0) * this.cost
		},
	},
	async created() {
		try {
			const response = await fetch('./quiz.json')
			const { items, results } = await response.json()

			this.items = items
			this.results = results
		} catch (_) {
			this.items.push({
				question: 'No questions :(',
			})
		}
	},
	methods: {
		play() {
			this.step = 'quiz'
		},
		next(value) {
			this.values.push(value)

			if (this.activeId + 1 < this.items.length) {
				this.activeId += 1
			} else {
				this.step = 'finish'
			}
		},
		restart() {
			this.activeId = 0
			this.values = []
			this.step = 'start'
		},
	},
}
</script>

<style lang="stylus" src="@styl/index.styl"></style>
